import axios from 'axios'
import noData from '@/components/noData'
import back from '@/components/back'
export default {
	components: {
		noData,
		back
	},
	data() {
		return {
			msg: "",
			msgCopy: "",
			showExpand: false,
			info: null,
			temp: false,
			drList: null,
			page: 1,
			haveMore: true,
			title:""
		};
	},
	created() {
		this.pull()
		this.pullDr(false)
	},
	beforeDestroy() {
		window.onscroll = null
	},
	mounted() {
		document.title = ""
		this.getMore()
	},
	methods: {
		pull() {
			this.$get('/PlatFormAPI/SpecialistHosGroup/QuerySpecialistHosGroup', { //获取专科团队明细
				SpecialistHosGroupId: this.$route.query.id
			}).then(o => {
				this.info = o
				this.msg = this.info.Description
				this.msgCopy = this.info.Description + "&nbsp;&nbsp;&nbsp;&nbsp;"
				if(this.msg && this.msg.length > 66) {
					this.showExpand = true
					this.msg = this.msg.slice(0, 66)
				}
				this.title = this.info.HosGroupName
			})
		},
		pullDr(h) {
			this.$post('/PlatFormAPI/SpecialistHosGroup/QuerySpecialistHosGroupMemberPage', { //专科团队医生(成员)/选择转诊医生/领衔专家
				SpecialistHosGroupId: this.$route.query.id,
				KeyName: "",
				IsHead: -1, //是否领衔。0不是；1是(-1查询所有,查询领衔专家的时候必须赋值1)
				IsSpecialReferral: -1,
				PageIndex: this.page,
				PageSize: 10
			}).then(o => {
				//领衔专家
				if(!o.ReturnData || o.ReturnData.length < 10) {
					this.haveMore = false
				} else {
					this.haveMore = true
				}
				if(h) {
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach(v => {
						this.drList.push(v)
					})
					this.temp = true
				} else {
					this.drList = o.ReturnData || []
					this.temp = true
				}
			})
		},
		getMore() {
			//加载更多
			const el = this.$refs.el
			let timer = null
			el.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = el.scrollTop ||
						el.pageYOffset ||
						el.scrollTop,
						h = el.scrollHeight,
						wh = el.offsetHeight
					if(h - top - wh < 100 && this.haveMore && this.temp) {
						this.temp = false
						this.page += 1
						this.pullDr(true)
					}
				}, 100)
			}
		},
		showExpandFun() {
			if(this.showExpand) {
				this.msg = this.msgCopy
			} else {
				this.msg = this.msg.slice(0, 66)
			}
			this.showExpand = !this.showExpand
		},
	}
};



